<template>
  <div class="detail-header">
    <router-link v-show="isShow" tap="div" to="/" class="detail-header-back iconfont icon-left"></router-link>
    <div :style="opacityStyle" v-show="!isShow" class="header-fixed">
      <router-link to="/" class="iconfont icon-left go-back"></router-link>
      杭州野生动物世界
    </div>
    <div class="content"></div>
  </div>
</template>
<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      isShow: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.windowScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.windowScroll)
  },
  methods: {
    windowScroll () {
      let top = document.documentElement.scrollTop
      if (top > 0) {
        let opacityVal = top / 88
        this.isShow = false
        opacityVal = opacityVal < 1 ? opacityVal : 1
        this.opacityStyle = {
          opacity: opacityVal
        }
      } else {
        this.isShow = true
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .content {
    height: 1500px;
  }
  .detail-header-back {
    position: fixed;
    left: 20px;
    top: 20px;
    width: 72px;
    height: 72px;
    background: rgba(0, 0, 0, .8);
    text-align: center;
    line-height: 72px;
    border-radius: 72px;
    color: #fff;
    font-size: 32px;
  }
   .header-fixed {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    height: 88px;
    background: #00bcd4;
    line-height: 88px;
    text-align: center;
    color: #fff;
    font-size: 32px;
    .go-back{
      font-size:40px;
      position: absolute;
      left: 10px;
      top: 0;
      color: #fff;
    }
  }
</style>



